import React, { useState, useRef, useEffect } from "react";
import {
  RichTooltip,
  RichTooltipProps,
} from "../../floating/rich-tooltil/rich-tooltip.component";
import { BaseButton } from "../../buttons/base-button/base-button.component";
import { ThemeProps } from "../../../shared/defaultProps";

interface FormatedTextProps extends RichTooltipProps {
  placeholder?: string;
  rlValue: string;
  onChanges: (value: string) => void;
  onInput: (value: string) => void;
}

export const FormatedTextField: React.FC<FormatedTextProps> = ({
  placeholder = "",
  rlValue = "",
  onChanges,
  onInput,
  disabled = false,
  theme = "light-theme",
  textRich,
  tailPlacement,
}) => {
  const [value, setValue] = useState(rlValue); // Обновляем начальное значение
  const [maxLength, setMaxLength] = useState(placeholder.length);
  const [richIsOpen, setOpen] = useState(false);
  const [onInputs, setOnInputs] = useState(false);
  const [constPlaceholder, setPlaceholder] = useState(placeholder);

  const fieldRef = useRef<HTMLInputElement | null>(null);
  const richTooltip = useRef<HTMLDivElement | null>(null);
  const errorIcon = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    if (value !== "" && rlValue && !onInputs) {
      setPlaceholder(""); // Обновляем placeholder, если значения есть
    } else if (!onInputs) {
      setPlaceholder(placeholder); // Восстанавливаем placeholder, если нет значений
    }
    onChanges(rlValue); // Передаём новое значение в onChanges
  }, [rlValue, value, placeholder, onInputs, onChanges]); // Добавляем все зависимости, чтобы не было "старая" информация

  const replaceSymbol = (event: React.FormEvent<HTMLInputElement>) => {
    setOnInputs(true);
    const index = event.currentTarget.value.length - 1;
    if (fieldRef.current) {
      if (event.currentTarget.value === "" || !event.currentTarget.value) {
        setPlaceholder(placeholder); // Восстановить placeholder, если поле пустое
      } else if (
        constPlaceholder === placeholder ||
        (event.currentTarget.value !== "" && constPlaceholder !== "")
      ) {
        const arrPlaceholder = constPlaceholder.split("");
        arrPlaceholder[index] = fieldRef.current.value[index];
        setPlaceholder(arrPlaceholder.join("")); // Обновить placeholder, если символ изменился
      }
    }
  };

  const onChangeInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (rlValue !== "" && rlValue) {
      setPlaceholder(""); // Если есть значение, то placeholder пустой
    } else {
      setPlaceholder(placeholder); // Восстанавливаем placeholder
    }
    onChanges(event.target.value); // Передаем изменённое значение
    setOnInputs(false);
  };

  const closeTooltip = () => {
    setOpen((prevState) => !prevState); // Переключаем видимость подсказки
  };

  return (
    <section className="formated-field">
      <span className="placeholder">{constPlaceholder}</span>
      <input
        ref={fieldRef}
        className="input"
        type="text"
        disabled={disabled}
        minLength={maxLength}
        value={value} // Убедитесь, что поле синхронизировано с состоянием
        onChange={onChangeInput}
        onInput={replaceSymbol}
      />
      <div ref={errorIcon} className="errorName">
        <BaseButton
          name="question_mark"
          size="base"
          theme={theme}
          onAction={closeTooltip}
        />
      </div>
      <div className="richTooltip" ref={richTooltip}>
        <RichTooltip 
            theme={theme}
            tailPlacement={tailPlacement}
            textRich={textRich}
            onHandleCloseClick={()=>closeTooltip}
        />
      </div>
    </section>
  );
};

export default FormatedTextField;
